::-webkit-scrollbar {
  /* transform: translateY(-10px);
  width: 1px; */
}

/* ::-webkit-scrollbar-track {

} */

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.App{
  color: #FFFFFF;
  font-family: 'Inter', sans-serif;
  width: auto;
  height: auto;
  transform: rotate(-90deg) translateX(-100vh);
  transform-origin: top left;
  overflow-y: scroll;
  overflow-x: hidden;
  position: absolute;
  
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.wrapper{
  display: flex;
  flex-direction: row;
  width: fit-content;
  height: fit-content;
  transform: rotate(90deg) translateY(-100vh);
  transform-origin: top left;
  overflow-y: hidden;
  overflow-x: hidden;
}

.section_dimensions{
  border-right: 1px solid #8CA5A1;
  width: 100vw;
  height: 100vw;
  text-align: center;
   overflow-x: hidden;
}




@media screen and  (max-width: 800px) {
      
    .App{
      transform: rotate(360deg) translateX(0vh);
      height: 100vh !important;
      width: 100vw !important; 
      /* overflow-y: unset;
      overflow-x: scroll;*/
      transform-origin: top bottom; 
    }

    .wrapper{
      flex-direction: column;
      transform: rotate(360deg) translateY(0vh);
      height: auto !important;
      width: 100vw !important;
      /* overflow-y: unset;
      overflow-x: scroll; */
      transform-origin: none !important;
    }
}
