.nav_bar{
    border-right:1px solid #E9EBEA;
    height: 100vh;
    width: 80px;
    position: relative;
}

.logo_bg{
    background: #E9EBEA;
    width: 80px;
    height: 90px;
    display: flex;
    align-items: center;
}

.logo{
    width: 48px;
    height: 42.58px;
    margin: 0 auto;
}

.toggle_theme{
    background-color: #8CA5A1;
    border-radius: 50%;
    height: 32px;
    width: 32px;
    position: absolute;
    bottom: 24px;
    left: 24px;
}